import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import './Event.css';



class Event extends Component {
  render() {
    const { t, style } = this.props;
    return (
      <div className="Event">
        <div className="Event-container">
            <img className="Event-photo" src={this.props.event.photo} alt={this.props.event.title} />
            <img className="Event-photoTransparent" src={this.props.event.photoTransparent} alt={this.props.event.title} />
          <div className="Event-textContainer">
            <img className="Event-title-marco" src={this.props.marcoServices} alt={this.props.event.title} />
            <p style={{ color: style.general }} className="Event-title">
              {t(this.props.event.title)}
            </p>
            <p style={{ color: style.subTitle }} className="Event-subtitle"> ({t(this.props.event.subtitle)})</p>
            <p style={{ color: style.general }} className="Event-description"> {t(this.props.event.description)}</p>
          </div>
        </div>
      </div>
    );
  }
}

export default withTranslation()(Event);
