import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';
import './PortfolioCompleted.css';



class PortfolioCompleted extends Component {
  render() {
    const { t, style } = this.props;
    return (
      <div className="portfolio-completed">
        <div className="container">
          <div className="marcoContainer">
            <p style={{ color: style.title }} className="title">
              {t(this.props.portfolioCompleted.title)}
              <img className="title-marco" id={`marco-${this.props.index}`} src={this.props.marcoServices} alt={t(this.props.portfolioCompleted.title)} />
            </p>
          </div>
          <p style={{ color: style.title }} className="link"> {this.props.portfolioCompleted.link}</p>
        </div>
      </div>
    );
  }
}

export default withTranslation()(PortfolioCompleted);
