import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import './Service.css';

class Service extends Component {
  render() {

    const { t, style } = this.props;

    return (
      <div className="Service">
        <div className="Service-container">
          <div className="Service-logoContainer">
            <img className="Service-logo" src={this.props.service.icon} alt={t(this.props.service.title)} />
            <p style={{ color: style.title }} className="Service-title">
              {t(this.props.service.title)}
              <img className="Service-title-marco" src={this.props.marcoServices} alt={t(this.props.service.title)} />
            </p>
          </div>
          <p style={{ color: style.general }} className="Service-description"> {t(this.props.service.description)}</p>
        </div>
      </div>
    );
  }
}

export default withTranslation()(Service);
