import React, { Component } from 'react';
import { withTranslation, Trans } from 'react-i18next';
import { withRouteData } from 'react-static'

import Description from "./description/Description";
import Service from "./service/Service";
import Client from "./client/Client";
import Technology from "./technology/Technology";
import PortfolioCompleted from "./portfolioCompleted/PortfolioCompleted";
import Portfolio from "./portfolio/Portfolio";

import Event from "./event/Event";
import RedesUnity from "./redes/Redes"

import './Unit.css'

// Custom styles from units (don't break generality):
import './styles/Unit-Soft.css'
import './styles/Unit-IoT.css'
import './styles/Unit-Games.css'
import './styles/Unit-Science.css'
import './styles/Unit-School.css'
import './styles/Unit-Media.css'


class Unit extends Component {

  render() {

    const { t, data } = this.props;

    return (
      <div className={`Unit ${data.unit}`} style={{
        background: `radial-gradient(ellipse at center,#fff 10%,${data.style.background} 100%)`
      }}>
        <Description unity={data} />
        {
          (data.services && data.services.active) ? (
            <div> {/* new component */}
              <p style={{ color: data.style.title }} className="title-services">{t('unit.services')}</p>
              <div className="container-services">
                {
                  data.services.backgrounds.map(
                    (back, index) =>
                      <img key={index} className={`background-services ${back.class}`} src={back.url} alt={`service-background-${index}`} />
                  )
                }
                {
                  data.services.list.map(
                    (service, index) =>
                      <div key={index} className="container-service">
                        <Service
                          service={service}
                          style={data.style}
                          marcoServices={data.marcoServices}
                        />
                      </div>
                  )
                }
              </div>
            </div>
          ) : ""
        }
        {
          (data.clients && data.clients.active) ? (
            <div> {/* new component */}
              <p style={{ color: data.style.title }} className="title-services">{t('unit.clients')}</p>
              <div className="container-clients">
                {
                  data.clients.list.map(
                    (client, index) => <Client key={index} client={client} color={data.style.general} />
                  )
                }
              </div>
            </div>
          ) : ""
        }
        {
          (data.portfolio && data.portfolio.active) ? (
            <div> {/* new component */}
              <p style={{ color: data.style.title }} className="title-services">{t('unit.portfolio')}</p>
              <div className="container-portfolioCompleted">
                {
                  data.portfolio.completed.map(
                    (portfolioCompleted, index) =>
                      <PortfolioCompleted
                        key={index}
                        index={index}
                        portfolioCompleted={portfolioCompleted}
                        style={data.style}
                        marcoServices={data.marcoServices}
                      />
                  )
                }
              </div>
              <div className="container-portfolioOngoing">
                {
                  data.portfolio.ongoing.map(
                    (portfolioOngoing, index) =>
                      <Portfolio
                        key={index}
                        index={index}
                        portfolioOngoing={portfolioOngoing}
                        marcoLargelOngoing={data.marcoLargelOngoing}
                        style={data.style}
                      />
                  )
                }
              </div>
            </div>
          ) : ""
        }

        {
          (data.technologies && data.technologies.active) ? (
            <div> {/* new component */}
              <p style={{ color: data.style.title }} className="title-services"><Trans i18nKey="unit.techs">1<br />2</Trans></p>

              <div className="container-allTechnologies">
                {
                  data.technologies.list.map(
                    (techType, index) => (
                      <div key={index} className="container-technologies">
                        {techType.name !== "default" ?
                          <p style={{ color: data.style.subTitle }} className="technology-title">{t(`${data.traduction}.technologies.${techType.name}`)}
                          </p>
                          : ""}
                        {techType.list.map(
                          (technology, index) =>
                            <div key={index} className="container-technology">
                              <Technology
                                technology={technology}
                                style={data.style}
                              />
                            </div>
                        )}
                      </div>
                    ))
                }
              </div>
            </div>
          ) : ""
        }
        {
          (data.events && data.events.active) ? (
            <div> {/* new component */}
              <p style={{ color: data.style.title }} className="title-services">{t('unit.events')}</p>
              <div className="container-events">
                {
                  data.events.list.map(
                    (event, index) =>
                      <Event
                        key={index}
                        event={event}
                        style={data.style}
                        marcoServices={data.marcoServices}
                      />
                  )
                }
              </div>
              <RedesUnity redes={data.redes} />
            </div>
          ) : ""
        }
      </div>
    );
  }
}

export default withRouteData(withTranslation()(Unit));
