import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import './Description.css';



class DescriptionClass extends Component {
  render() {

    const { t, i18n } = this.props;

    return (
      <div className="description">
        <div className="container">
          <img className="puntos" src={this.props.unity.puntos} alt={this.props.unity.name}></img>
          <div className="logo-container">
            <img className="logo" src={this.props.unity.logo} alt={this.props.unity.name}></img>
          </div>
          <p style={{color: this.props.unity.color}} className="description-text"> {t(this.props.unity.description)}</p>
          <img className="icon" src={this.props.unity.icon} alt={this.props.unity.name}></img>
        </div>
      </div>
    );
  }
}

const UnityDescription = withTranslation()(DescriptionClass);

export default UnityDescription;