import React, { Component } from 'react';
import './Technology.css';



class Technology extends Component {
  render() {
    const { style } = this.props

    return (
      <div className="Technology">
        <div className="Technology-container">
          <div className="Technology-iconContainer">
            <img className="Technology-icon" src={this.props.technology.icon} alt={this.props.technology.title} />
            <p style={{ color: style.general }} className="Technology-title">
              {this.props.technology.title}
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default Technology;